.App {
  text-align: center;
}
.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}
.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}
.up {margin-top: 15px}
.up2 {margin-top: 30px}
.icons{    font-size: 50px;margin: 15px 0px 0px 50px;}
.info{background: darkseagreen;padding: 33px;}
.custom_row{margin-left: 0px;margin-right: 0px}
.selected{font-weight: bold;}
.iconspan{margin-right: 10px}
.icondiv{padding: 20px}
.footerline{ text-align: center;color: wheat;margin-top: 15%;}
.footer{background: #052424}
.footertext{color: wheat;margin-top: 4%}
.twitter-social-icon .social-svg {color:white;background:#00aced}
.linkdin-social-icon .social-svg {color:white;background: #0077b5}
.github-social-icon .social-svg {color:white;background:black}
.insta-social-icon .social-svg {color:white;background: #f05a27}
.form{width: 50%;height: 500px; margin-left: auto;border: 1px solid dimgrey;margin-right: auto;padding: 50px;margin-top: 4%;margin-bottom: 4%;border-radius: 20px;}
.signup{padding: 12px;color: white;background-color: #2ebcbc;}
.getstart{padding: 12px;color: white;background-color: #b62d63c4;}
.login{padding: 12px;color: white;background-color: #3d4153;}
input{padding: 10px;background: #3d4153;width: 100%;border: 1px solid #2ebcbc;}


        


